body {
  margin: 0;
  padding: 0;
  font-family: Helvetica, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


#name {
  color: white;
  text-align: center;
  font-size: 9vw; }

#name .title {
  font-size: 5vw;
  margin: 0; }

#name .rocket {
  width: 7vw;
  height: 7vw; }

#name .left {
  display: inline-block; }

#name .right {
  display: inline-block; }

#nav-bar {
  padding: 1%; }

#nav-bar div {
  display: inline-block; }

#nav-bar .quote::before {
  content: "'"; }

#nav-bar .quote::after {
  content: "'"; }

#nav-bar .quote {
  color: white;
  font-size: 1.95em;
  font-style: italic;
  margin-left: 1%; }

#nav-bar .logo {
  width: 48px;
  height: 48px;
  float: left; }

#nav-bar img:hover {
  cursor: pointer; }

#intro {
  background-color: #f4ad42;
  padding-bottom: 0.1%; }

#intro #name {
  padding-top: 10%; }

.arrow {
  font-size: 0.5em; }

.arrow a {
  color: white;
  text-decoration: none; }

.tag {
  color: white;
  cursor: pointer;
  display: inline-block;
  padding: 0.5%;
  margin-right: 1%;
  width: 100px; }

.fav {
  color: black;
  background-color: white;
  border: 2px solid black; }

.blue {
  background-color: #089caa; }

.dark-blue {
  background-color: #1770d6; }

.green {
  background-color: #09990b; }

.red {
  background-color: #a83305; }

.wide {
  width: 170px; }

#languages li {
  list-style: none;
  font-size: 1.7em;
  margin-top: 2%; }

